<template>
  <!-- <n-config-provider :theme="darkTheme" :locale="zhCN" :date-locale="dateZhCN"> -->
  <div class="">
    <div v-if="showResult" class="result">
      <div class="result-summary">
        共计{{ questions.length }}道题目， 作答{{ myAnswer.filter((v) => v != undefined).length }}道，答对{{
          myAnswer.filter((v, idx) => v != undefined && v === questions[idx].answer).length
        }}道，答错{{ myAnswer.filter((v, idx) => v != undefined && v !== questions[idx].answer).length }}道.
      </div>
      <div class="result-detail">
        <n-button type="default" class="status" v-for="(question, idx) in questions" :key="idx" @click="go(idx)">
          {{ `${idx + 1}.` }}
          <n-icon size="24" color="#70c0e8" v-if="myAnswer[idx] == undefined">
            <help-circle-sharp />
          </n-icon>
          <n-icon size="24" color="#63e2b7" v-if="myAnswer[idx] != undefined && myAnswer[idx] === question.answer">
            <checkmark-circle-sharp />
          </n-icon>
          <n-icon size="24" color="#e88080" v-if="myAnswer[idx] != undefined && myAnswer[idx] !== question.answer">
            <close-circle-sharp />
          </n-icon>
        </n-button>
      </div>
    </div>
    <div v-if="!showResult">
      <div class="header">
        <n-button type="default" size="large" @click="go(index - 1)" :disabled="index === 0">上一题</n-button>
        <div class="timer">Doris</div>
        <n-button type="default" size="large" @click="go(index + 1)" :disabled="index === questions.length - 1"
          >下一题</n-button
        >
      </div>
      <n-card size="medium" embedded class="card-detail">
        <template #header>
          <n-icon size="24" color="#70c0e8" class="status-icon" v-if="myAnswer[index] == undefined">
            <help-circle-sharp />
          </n-icon>
          <n-icon
            size="24"
            color="#63e2b7"
            class="status-icon"
            v-if="myAnswer[index] != undefined && myAnswer[index] === questions[index].answer"
          >
            <checkmark-circle-sharp />
          </n-icon>
          <n-icon
            size="24"
            color="#e88080"
            class="status-icon"
            v-if="myAnswer[index] != undefined && myAnswer[index] !== questions[index].answer"
          >
            <close-circle-sharp />
          </n-icon>
          {{ `${index + 1}.` }}
          {{ questions[index].title }}
        </template>
        <n-space vertical>
          <n-radio
            class="option"
            v-for="(option, idx) in questions[index].options"
            :key="idx"
            :checked="idx === myAnswer[index]"
            @click="answer(index, idx)"
          >
            {{ `${String.fromCharCode("A".charCodeAt() + idx)}. ${option}` }}
          </n-radio>
        </n-space>
        <template #footer>
          <n-collapse display-directive="show">
            <n-collapse-item title="答案">
              <div>
                {{ String.fromCharCode("A".charCodeAt() + questions[index].answer) }}
              </div>
              <div>
                {{ questions[index].analysis }}
              </div>
            </n-collapse-item>
          </n-collapse>
        </template>
      </n-card>
      <div class="result-footer">
        <n-button type="default" size="medium" @click="showResult = true">查看结果</n-button>
      </div>
    </div>
  </div>
  <!-- </n-config-provider> -->
</template>

<script>
import { defineComponent } from "vue";
import { NButton, NCard, NIcon, NRadio, NSpace, NCollapse, NCollapseItem } from "naive-ui";
import { CheckmarkCircleSharp, CloseCircleSharp, HelpCircleSharp } from "@vicons/ionicons5";
import questions from "../data/data.json";

export default defineComponent({
  data() {
    return {
      questions,
      index: 0,
      showAnswer: true,
      myAnswer: [],
      showResult: false,
    };
  },
  components: {
    NButton,
    NCard,
    NIcon,
    NRadio,
    NSpace,
    NCollapse,
    NCollapseItem,
    CheckmarkCircleSharp,
    CloseCircleSharp,
    HelpCircleSharp,
  },
  methods: {
    answer(index, idx) {
      this.myAnswer[index] = idx;
    },
    go(idx) {
      this.showResult = false;
      this.index = idx;
    },
  },
});
</script>

