.header {
  display: flex;
  align-items: center;
}
.timer {
  flex: 1;
  width: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-detail {
  margin: 16px 10px;
  width: calc(100vw - 20px);
}
.status-icon {
  transform: translateY(4px);
}
.result-footer {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  /* align-items: center; */
}
.result {
  position: fixed;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.result-summary {
  flex: none;
  padding: 20px 10px;
}
.result-detail {
  height: 0;
  flex: 1;
  overflow: scroll;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 10px;
}
.status {
  height: 32px;
  display: inline-flex;
  width: 18%;
  justify-content: center;
  align-items: center;
  border: 1px solid #1f1f1f;
  margin-bottom: 10px;
}
</style>
<style>
.n-radio .n-radio__dot {
  align-self: flex-start;
  flex: none;
}
